import d from './data.json';
import pngImages from '../images/destination/*.png';
import webpImages from '../images/destination/*.webp';

function main() {

    const links = document.querySelectorAll('.destination__link');
    const title = document.getElementById('title');
    const p = document.getElementById('description');
    const distance = document.getElementById('distance');
    const travel = document.getElementById('travel');
    const picture = document.getElementById('destination__image');
    const image = document.querySelector('.destination__image');

    let c = 'moon';
    let data = d;
    const dest = {
        "moon": 0,
        "mars": 1,
        "europa": 2,
        "titan": 3
    }

    links.forEach(elm => {
        elm.addEventListener('click', handleLink);
    });




    function handleLink(e) {
        links.forEach(elm => {
            elm.classList.remove('active');
        });


        e.target.parentElement.classList.add('active');
        c = e.target.textContent;
        let sel = data.destinations[dest[c]];
        title.textContent = sel.name;
        p.textContent = sel.description;
        distance.textContent = sel.distance;
        travel.textContent = sel.travel;
        image.setAttribute('id', 'fade-in');
        p.setAttribute('class', 'scale');
        title.classList.add('zoomIn');
        setTimeout(() => {
            image.setAttribute('id', '');
            p.setAttribute('class', '');
            title.classList.remove('zoomIn');
        }, 700)
        handleImage('/', sel.images.webp, sel.images.png, picture);



    }

    function handleImage(path, webp, png, picture) {
        picture.children[0].srcset = webpImages[webp];
        picture.children[1].srcset = pngImages[png];
        picture.children[2].src = path + png;
    }


}

window.addEventListener('DOMContentLoaded', main);